const randomNum = (min, max) => Math.floor(Math.random() * (max - min + 1)) + min;

const randomSort = (arr) => arr.sort(() => Math.random() - 0.5);

const shuffle = (arr) => {
  let m = arr.length, t, i;
  while (m) {
    i = Math.floor(Math.random() * m--);
    t = arr[m];
    arr[m] = arr[i];
    arr[i] = t;
  }
  return arr;
};

export default {
  randomNum,
  randomSort,
  shuffle,
}
